<template>
  <Modal
    :active="showModal"
    :event-name="'product-details-table-modal'"
    :custom-width="'w-full ipad:w-[768px] ipadPro:w-[1024px]'"
    :custom-horizontal-padding="'px-spacing-0'"
    :custom-vertical-padding="'py-spacing-300'"
    :full-screen="'nexus'"
  >
    <div class="relative h-full w-full">
      <ProductDetailsTable
        :bg-class="'bg-bg-neutral-default'"
        class="ipad:h-[85dvh]"
      />
    </div>
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
export default {
  components: {
    Modal,
    ProductDetailsTable: () => import("@/components/ProductDetails/Table.vue"),
  },
  data() {
    return {
      showModal: false,
    };
  },
  mounted() {
    this.$bus.$on("product-details-table-modal", (data) => {
      if (data?.showModal) {
        this.showModal = data?.showModal;
        if (data?.productId) {
          setTimeout(() => {
            this.$bus.$emit("fetch-product-details", { id: data?.productId });
          }, 0);
        }
      } else {
        this.showModal = false;
      }
    });
  },
  methods: {
    close() {
      this.$bus.$emit("product-details-table-modal", { showModal: false });
      this.$bus.$emit("set-selected-product", null);
    },
  },
  beforeDestroy() {
    this.$bus.$off("product-details-table-modal");
  },
};
</script>
